:root {
  --gutter: 3rem;
  --font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  --font-family--monospace: ui-monospace, SFMono-Regular, 'SF Mono', Menlo, Consolas, 'Liberation Mono', monospace;
  --font-size: 1.4rem;
  --line-height: 1.6;
  --color--background: #F7F7F7;
  --color--highlight: #FFEC3D66;
  --color--border: #D8DEE4;
  --color--text: #191919;
  --color--dim: rgba(25, 25, 25, 0.2); }
  @media screen and (max-width: 600px) {
    :root {
      --gutter: 2.5rem;
      --font-size: 1.4rem;
      --line-height: 1.6; } }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  font-size: 62.5%;
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  width: 100%; }

body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  color: var(--color--text);
  background: var(--color--background);
  font-family: var(--font-family);
  font-size: var(--font-size);
  line-height: var(--line-height);
  overscroll-behavior: none; }
  @media screen and (max-width: 600px) {
    body {
      overscroll-behavior: auto;
      width: 100vw; } }

::selection {
  background: var(--color--highlight); }

a {
  text-decoration: none;
  color: blue; }

.bold {
  font-weight: 600; }

input[type=password],
input[type=search],
input[type=text] {
  width: auto;
  margin: 0;
  padding: 0;
  overflow: visible;
  color: inherit;
  font: inherit;
  line-height: inherit;
  background: transparent;
  border: none;
  appearance: none;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  color: var(--color--text);
  background-color: white;
  padding: calc(0.5 * var(--gutter)) var(--gutter);
  border: 1px solid var(--color--border); }
  input[type=password]::-moz-focus-inner,
  input[type=search]::-moz-focus-inner,
  input[type=text]::-moz-focus-inner {
    padding: 0;
    border: 0; }
  input[type=password]:focus,
  input[type=search]:focus,
  input[type=text]:focus {
    outline: none; }
  input[type=password]:focus,
  input[type=search]:focus,
  input[type=text]:focus {
    outline: 0; }

input[type=search].filter {
  width: 100%;
  border: 0;
  background: transparent; }
  @media screen and (min-width: 600px) {
    input[type=search].filter:not(:focus):placeholder-shown {
      opacity: 0;
      pointer-events: none; } }
  input[type=search].filter::placeholder {
    color: var(--color--dim); }

[data-filterable].is-filtered [data-filter]:not(.is-result) {
  display: none; }

[data-filterable].is-filtered {
  flex: 1; }

.swiper {
  overflow-x: scroll;
  overflow-y: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
  display: flex;
  scroll-snap-type: x; }
  .swiper::-webkit-scrollbar {
    display: none;
    height: 0;
    background: transparent; }
  @media screen and (max-width: 600px) {
    .swiper {
      direction: rtl; }
      .swiper * {
        direction: ltr; } }
  .swiper > *:first-child {
    scroll-snap-align: start;
    flex-grow: 2;
    flex-shrink: 0;
    flex-basis: 100%; }
  .swiper__actions {
    display: flex; }
  .swiper__actions button {
    width: auto;
    margin: 0;
    padding: 0;
    overflow: visible;
    color: inherit;
    font: inherit;
    line-height: inherit;
    background: transparent;
    border: none;
    appearance: none;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    scroll-snap-align: start end;
    color: blue;
    cursor: pointer;
    margin: 0 1ch; }
    .swiper__actions button::-moz-focus-inner {
      padding: 0;
      border: 0; }
    .swiper__actions button:focus {
      outline: none; }
    .swiper__actions button + button {
      margin-left: 0; }

body[data-template=list] {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 3em; }
  body[data-template=list] .list {
    list-style: none;
    padding: var(--gutter);
    margin: 0 auto;
    width: 100%;
    max-width: 600px; }
    body[data-template=list] .list li {
      margin: 0.3em 0; }
    body[data-template=list] .list__item {
      display: flex;
      align-items: center;
      text-decoration: none;
      color: var(--color--text);
      overflow: hidden;
      white-space: nowrap;
      width: fit-content;
      max-width: 100%; }
      @media screen and (max-width: 600px) {
        body[data-template=list] .list__item {
          width: 100%; } }
    body[data-template=list] .list__item:focus, body[data-template=list] .list__item:hover {
      outline: 0;
      background: var(--color--highlight); }
    body[data-template=list] .list__item-favicon {
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      user-select: none;
      height: 1em;
      width: 1em;
      margin-right: 1ch; }
    body[data-template=list] .list__item-title {
      overflow: hidden;
      text-overflow: ellipsis; }
    body[data-template=list] .list__item-hostname {
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      user-select: none;
      margin-left: 1ch;
      color: var(--color--dim); }
      @media screen and (max-width: 600px) {
        body[data-template=list] .list__item-hostname {
          display: none; } }
    body[data-template=list] .list label[for=showAll] {
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      user-select: none;
      display: none;
      position: relative;
      cursor: pointer;
      float: right;
      color: var(--color--dim);
      margin-top: 1em; }
      body[data-template=list] .list label[for=showAll]:hover {
        color: blue; }
    body[data-template=list] .list li:nth-child(20) ~ label[for=showAll] {
      display: block; }
    body[data-template=list] .list #showAll,
    body[data-template=list] .list #showAll:not(:checked) ~ li:nth-child(20) ~ li,
    body[data-template=list] .list #showAll:checked ~ label[for=showAll] {
      display: none; }
  body[data-template=list] footer {
    z-index: 1;
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    align-items: center;
    border-top: 1px solid var(--color--border);
    background: var(--color--background); }
    @media screen and (min-width: 600px) {
      body[data-template=list] footer:not(:focus-within) {
        border: 0;
        background: transparent; } }
    body[data-template=list] footer:focus-within {
      background: white; }
    body[data-template=list] footer input[type=search] {
      padding-right: 0; }
    body[data-template=list] footer a {
      padding: 0 var(--gutter); }
      body[data-template=list] footer a:not(:hover) {
        color: var(--color--dim); }

body[data-template=authenticate] {
  display: grid;
  place-items: center; }
  body[data-template=authenticate] label {
    display: block;
    font-size: var(--font-size--small);
    margin-bottom: 0.5em; }
